@import url(https://fonts.googleapis.com/css?family=Lato:300,400);

@import url(https://fonts.googleapis.com/css?family=Fira+Mono:400,700);

*,
*:after,
*:before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  display: flex;
  color: #585858;
  background-color: #fefefe;
  font-family: 'Fira Mono', monospace;
  font-size: 1.6em;
  font-weight: 400;
  letter-spacing: 0.0625em;
  line-height: 1.8em;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  body {
    font-size: 1.4em;
    line-height: 1.6em;
  }
}

a {
  font-weight: 700;
  color: #444444;
  text-decoration: none;
}

a:focus,
a:hover {
  text-decoration: underline;
}

p {
  margin: 1.6rem 0 1.6rem 0;
}

p a {
  font-weight: 400;
  color: #444444;
  text-decoration: underline;
  text-underline-position: under;
}

p a:focus,
p a:hover {
  color: #3366CC;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #444444;
  text-transform: uppercase;
  letter-spacing: 0.0625em;
  margin: 3.2rem 0 1.6rem 0;
}

h1 {
  font-size: 3.2rem;
  line-height: 3.2rem;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  h1 {
    font-size: 2.8rem;
    line-height: 2.8rem;
  }
}

h2 {
  font-size: 2.8rem;
  line-height: 2.8rem;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  h2 {
    font-size: 2.4rem;
    line-height: 2.4rem;
  }
}

h3 {
  font-size: 2.4rem;
  line-height: 2.4rem;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  h3 {
    font-size: 2.0rem;
    line-height: 2.0rem;
  }
}

h4 {
  font-size: 2.2rem;
  line-height: 2.2rem;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  h4 {
    font-size: 1.8rem;
    line-height: 1.8rem;
  }
}

h5 {
  font-size: 2.0rem;
  line-height: 2.0rem;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  h5 {
    font-size: 1.6rem;
    line-height: 1.6rem;
  }
}

h6 {
  font-size: 1.4rem;
  line-height: 1.4rem;
}

pre {
  margin: 1.6rem 0 1.0rem 0;
  padding: 1.6rem;
  overflow-x: auto;
}

code {
  display: inline-block;
  background-color: #444444;
  color: #fefefe;
  padding: 0.4rem 0.8rem 0.4rem 0.8rem;
}

blockquote {
  border-left: 2px solid #dcdcdc;
  padding-left: 1.6rem;
  font-style: italic;
}

th,
td {
  padding: 1.6rem;
}

table {
  border-collapse: collapse;
}

table td,
table th {
  border: 2px solid #444444;
}

table tr:first-child th {
  border-top: 0;
}

table tr:last-child td {
  border-bottom: 0;
}

table tr td:first-child,
table tr th:first-child {
  border-left: 0;
}

table tr td:last-child,
table tr th:last-child {
  border-right: 0;
}

img {
  max-width: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}

.container {
  margin: 0 auto;
  max-width: 120.0rem;
  width: 100%;
  padding-left: 2.0rem;
  padding-right: 2.0rem;
}

.float-right {
  float: right;
}

.float-left {
  float: left;
}

.content {
  flex: 1;
  margin-top: 1.6rem;
  margin-bottom: 3.2rem;
}

.content article header {
  margin-top: 3.2rem;
  margin-bottom: 3.2rem;
}

.content article header h1,
.content article header h2 {
  margin: 0;
}

.content article header h2 {
  margin-top: 1.0rem;
  font-size: 1.8rem;
  color: #585858;
}

@media only screen and (max-device-width: 768px) {
  .content article header h2 {
    font-size: 1.6rem;
  }
}

.avatar img {
  width: 20rem;
  height: auto;
  border-radius: 50%;
}

@media only screen and (max-device-width: 768px) {
  .avatar img {
    width: 10rem;
  }
}

.list ul {
  margin: 3.2rem 0 3.2rem 0;
  list-style: none;
  padding: 0;
}

.list ul li {
  font-size: 1.6rem;
}

@media only screen and (max-device-width: 768px) {
  .list ul li {
    font-size: 1.4rem;
    margin: 1.6rem 0 1.6rem 0;
  }
}

.list ul li span {
  display: inline-block;
  width: 20.0rem;
  text-align: right;
  margin-right: 3.0rem;
}

@media only screen and (max-device-width: 768px) {
  .list ul li span {
    display: block;
    text-align: left;
  }
}

.list ul li a {
  text-transform: uppercase;
}

.centered {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.centered .about {
  text-align: center;
}

.centered .about h1 {
  margin-top: 2.0rem;
  margin-bottom: 0.5rem;
}

.centered .about h2 {
  margin-top: 1.0rem;
  margin-bottom: 0.5rem;
  font-size: 2.4rem;
}

@media only screen and (max-device-width: 768px) {
  .centered .about h2 {
    font-size: 2.0rem;
  }
}

.centered .about ul {
  list-style: none;
  margin: 3.0rem 0 1.0rem 0;
  padding: 0;
}

.centered .about ul li {
  display: inline-block;
  position: relative;
}

.centered .about ul li a {
  text-transform: uppercase;
  margin-left: 1.0rem;
  margin-right: 1.0rem;
  font-size: 1.6rem;
}

@media only screen and (max-device-width: 768px) {
  .centered .about ul li a {
    font-size: 1.4rem;
  }
}

.centered .about ul li a i {
  font-weight: 400;
}

.centered .error {
  text-align: center;
}

.centered .error h1 {
  margin-top: 2.0rem;
  margin-bottom: 0.5rem;
  font-size: 4.6rem;
}

@media only screen and (max-device-width: 768px) {
  .centered .error h1 {
    font-size: 3.2rem;
  }
}

.centered .error h2 {
  margin-top: 2.0rem;
  margin-bottom: 3.2rem;
  font-size: 3.2rem;
}

@media only screen and (max-device-width: 768px) {
  .centered .error h2 {
    font-size: 2.8rem;
  }
}

.navigation {
  height: 6.0rem;
  width: 100%;
}

.navigation a,
.navigation span {
  display: inline;
  text-transform: uppercase;
  line-height: 6.0rem;
  letter-spacing: 0.1rem;
}

@media only screen and (max-device-width: 768px) {
  .navigation a,
  .navigation span {
    font-size: 1.4rem;
  }
}

.navigation .navigation-list {
  float: right;
  list-style: none;
  margin-bottom: 0;
  margin-top: 0;
}

@media only screen and (max-device-width: 768px) {
  .navigation .navigation-list {
    position: absolute;
    top: 6.0rem;
    right: 0;
    z-index: 5;
    visibility: hidden;
    opacity: 0;
    padding: 0;
    max-height: 0;
    width: 100%;
    background-color: #fefefe;
    border-top: solid 2px #dcdcdc;
    border-bottom: solid 2px #dcdcdc;
    transition: opacity 0.25s, max-height 0.15s linear;
  }
}

.navigation .navigation-list .navigation-item {
  float: left;
  margin: 0;
  position: relative;
}

@media only screen and (max-device-width: 768px) {
  .navigation .navigation-list .navigation-item {
    float: none !important;
    text-align: center;
  }

  .navigation .navigation-list .navigation-item a,
  .navigation .navigation-list .navigation-item span {
    line-height: 5.0rem;
  }
}

.navigation .navigation-list .navigation-item a,
.navigation .navigation-list .navigation-item span {
  margin-left: 1.0rem;
  margin-right: 1.0rem;
}

@media only screen and (max-device-width: 768px) {
  .navigation .navigation-list .menu-separator {
    border-top: 2px solid #585858;
    margin: 0 8.0rem;
  }

  .navigation .navigation-list .menu-separator span {
    display: none;
  }
}

.navigation #menu-toggle {
  display: none;
}

@media only screen and (max-device-width: 768px) {
  .navigation #menu-toggle:checked + label {
    color: #dcdcdc;
  }

  .navigation #menu-toggle:checked + label + ul {
    visibility: visible;
    opacity: 1;
    max-height: 100rem;
  }
}

.navigation .menu-button {
  display: none;
}

@media only screen and (max-device-width: 768px) {
  .navigation .menu-button {
    display: block;
    font-size: 2.4rem;
    font-weight: 400;
    line-height: 6.0rem;
    color: #585858;
    cursor: pointer;
  }
}

.pagination {
  margin-top: 6.0rem;
  text-align: center;
}

.pagination li {
  display: inline;
  text-align: center;
}

.pagination li span {
  margin: 0;
  text-align: center;
  width: 3.2rem;
}

.pagination li a span {
  margin: 0;
  text-align: center;
  width: 3.2rem;
}

.footer {
  width: 100%;
  text-align: center;
  line-height: 2.0rem;
  margin-bottom: 1.0rem;
}

/* AMAZING INTERACTIVE CANVAS */

#interactive-canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}

/* END INTERACTIVE CANVAS */

/* BRANDING LOGO */

#branding {
  font-family: 'Lato', Helvetica, Arial, sans-serif;
  display: inline-block;
  vertical-align: top;
  -webkit-transition: 300ms ease-in-out;
  -moz-transition: 300ms ease-in-out;
  -ms-transition: 300ms ease-in-out;
  -o-transition: 300ms ease-in-out;
  transition: 300ms ease-in-out;
}

#branding a {
  font-size: 2.6rem;
  letter-spacing: normal;
  line-height: 1em;
  border-top: 5px solid transparent;
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #444444;
}

#branding h2 {
  font-size: 1.2rem;
  font-weight: 100;
  letter-spacing: .01em;
  word-spacing: .1em;
  text-transform: uppercase;
  color: #828282;
  margin: 0;
  transform: translateY(-4px);
}

.branding-curly-brackets {
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-size: 3.3rem;
  transition: 200ms opacity linear;
}

#branding-name span:first-child {
  font-weight: 200;
}

#branding-name span:last-child {
  font-weight: 400;
  transition: 100ms color linear;
}

#branding:hover #branding-name span:last-child,
#branding.no-brackets #branding-name span:last-child {
  color: #D62A49;
}

#branding.no-brackets .branding-curly-brackets {
  opacity: 0;
}

.subtitle {
  font-weight: 400;
  font-size: 20px;
  color: #444;
}

.navigation #branding a {
  font-size: 1.6rem;
  font-family: 'Fira Mono';
}

.navigation #branding .branding-curly-brackets {
  font-size: 2.3rem;
}

/* END BRANDING LOGO */




